@import './mixins.scss';
@import './variables.scss';

.contact {
    margin-top: 2rem;
    position: relative;

    &_line1 {
        position: absolute;
        width: 2px;
        background-color: $green;
        top:10%;
        height: 90%;
        right: 5%;
    }

    &_line2 {
        position: absolute;
        top: 10%;
        height: 2px;
        background-color: $green;
        width: 30%;
        right: 5%;
    }

    &_title {
        @include title_section;
        margin-top: 1rem;
    }

    &_text {
        @include text_generic;
        padding-top: 2rem;
        font-size: 0.8rem;
        color: white;
        max-width: 80vw;
        margin: auto;
    }

    &_container {
        margin: 1rem auto;
        padding: 0% 2% 8% 5%;
        width: 70%;
        background-color: $black;
        display: flex;
        flex-direction: column;
        position: relative;


        &_git {
            &_link {
                @include text_generic;
                display: flex;
                align-items: center;
                text-decoration: none;
                font-size: 0.8rem;

                &_icon {
                    width: 2rem;
                    margin-right: 0.5rem;
                }
            }
        }

        &_mail {
            &_link {
                @include text_generic;
                text-align: left;
                display: flex;
                align-items: center;
                text-decoration: none;
                font-size: 0.8rem;

                &_icon {
                    width: 1.2rem;
                    margin-right: 0.5rem;
                }
            }
        }

        &_linkedin {
            &_link {
                @include text_generic;
                display: flex;
                align-items: center;
                text-decoration: none;
                font-size: 0.8rem;

                &_icon {
                    width: 2rem;
                    margin-right: 0.5rem;
                }
            }
        }
    }
    
}

@media(min-width:768px){
    .contact {
        margin-top: 6rem;
        scroll-snap-align: start;
        min-height: 70vh;

        &_line1 {
                height:60%;
                right: 5%;
                top:10%;
            }
        
            &_line2 {
                top: 10%;
                width: 45%;
                right: 5%;
            }

        &_title {
            @include title_section;
            margin-top: 4rem;
        }
        &_text{
            @include text_generic;
            padding-top: 2rem;
            font-size: 0.9rem;
            color:white;
            max-width: 50vw;
            margin: auto;
        }

        &_container {
            padding: 0% 2% 5% 5%;
            margin: 2rem auto;
            width: 50%;            

            &_git {
                &_link {
                    font-size: 0.9rem;
                    &_icon {
                        width: 2.5rem;
                        margin-right: 1rem;
                    }
                }
            }

            &_mail {
                &_link {
                    font-size: 0.9rem;

                    &_icon {
                        width: 1.7rem;
                        margin-right: 1rem;
                    }
                }
            }

            &_linkedin {
                &_link {
                    font-size: 0.9rem;
                    &_icon {
                        width: 2.5rem;
                        margin-right: 1rem;
                    }
                }
            }
        }

    }
}

@media(min-width:1200px){
    .contact {
        margin-top: 6rem;

        &_line1 {
            height: 70%;
            right: 5%;
            top:10%;
        }

        &_line2 {
            top: 10%;
            width: 45%;
            right: 5%;
        }

        &_title {
            margin-top: 5rem;
        }
        &_text{
            font-size: 1rem;
            line-height: 1.2rem;
        }


        &_container {
            // padding: 0% 2% 2.5% 4%;
            width: 30%;
            @include flex_center;

            &_git {
                width: 100%;
                &_link {
                    font-size: 1rem;
                    &_icon {
                        width: 2.5rem;
                    }
                }
            }

            &_mail {
                width: 100%;
                &_link {
                    font-size: 1rem;
                    &_icon {
                        width: 1.7rem;
                    }
                }
            }

            &_linkedin {
                width: 100%;
                &_link {
                    font-size: 1rem;
                    &_icon {
                        width: 2.5rem;
                    }
                }
            }
        }
    }
}
