@import './mixins.scss';
@import './variables.scss';

        .header {
            height: 100vh;
            width: 100vw;
            background-color: $gray;
            @include flex_center;
            font-family: $font-generic;
            position: relative;
    
            &_line1 {
                background-color: $green;
                height: 2px;
                width: 90%;
                position: absolute;
                top: 10%;
            }
    
    
            &_line2 {
                background-color: $green;
                width: 2px;
                position: absolute;
                top: 10%;
                right: 5%;
                height: 80%;
            }
    
            &_container {
                width: 80vw;
                margin: auto;
                height: 70vh;
                background-color: $black;
                display: flex;
                justify-content: center;
                flex-direction: column;
                flex-wrap: wrap;
    
                &_titles {
                    max-width: 100%;
                    @include flex_center;
                    flex-wrap: wrap;
    
                    &_title {
                        text-transform: uppercase;
                        letter-spacing: 1rem;
                        transform: scale(.8, 1);
                        font-size: 2rem;
                        color: white;
                    }
    
                    &_title2 {
                        color: $green;
                        text-transform: uppercase;
                        letter-spacing: 1rem;
                        transform: scale(.8, 1);
                        font-size: 2rem;
                    }
                }
    
                &_subtitle {
                    @include title_section;
                    text-align: center;
                    margin-top: 2rem;
                    color: white;
                    display: block;
                    font-weight: 100;
                    font-size: 2rem;
                    max-width: 100%;
                }
                
            }

            &_arrow {
                border-radius: 50%;
                background-color: white;
                position: absolute;
                bottom: 10%;
                min-width: 3.5rem;
                min-height: 3.5rem;
                display: flex;
                justify-content: center;
                align-items: center;

                &_icon {
                    width: 2.5rem;
                    rotate: 270deg;
                }

            }
        }
    
        .construction {
            font-weight: 400;
            color: red;
            max-width: 100%;
        }

@media (min-width: 768px) {
    .header {
        scroll-snap-align: start;
        &_container {
            &_titles {
                &_title {
                    font-size: 2.4rem;
                }
                &_title2 {
                    font-size: 2.4rem;
                }
            }

            &_subtitle {
                margin-top: 2rem;
                font-size: 2.2rem;
            }
        }
        &_arrow {
            min-width: 4.5rem;
            min-height: 4.5rem;
            &_icon {
                width: 3.5rem;
                rotate: 270deg;
            }

        }
    }
}

@media (min-width: 1200px) {
.header {

        &_container {
  
            &_titles {
 
                &_title {
                    @include title_header;
                }

                &_title2 {
                    @include title_header;
                }
            }

            &_subtitle {
                @include title_section;
                margin-top: 3rem;
                font-weight: 100;
            }
        }
        &_arrow {
            min-width: 5.5rem;
            min-height: 5.5rem;
            &_icon{
                width: 4.5rem;
                rotate: 270deg;
            }

        }
    }

    .construction {
        font-weight: 400;
        color: red;
    }

}